import React, {useEffect, useState} from "react";
import {makeStyles} from "@material-ui/core/styles";
import styles from "assets/jss/material-kit-react/views/executors.js";
// core components
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import List from "@material-ui/core/List";


import {
    Box,
    Fab,
    ListItem,
} from '@material-ui/core';

import Grid from "@material-ui/core/Grid";
import {firestore} from "../../firebase";
import EmptyState from "../../baseComponents/EmptyState";
import {ReactComponent as ErrorIllustration} from "../../illustrations/error.svg";
import {Refresh as RefreshIcon} from "@material-ui/icons";
import Loader from "../../baseComponents/Loader";

import {ReactComponent as NoDataIllustration} from "../../illustrations/no-data.svg";
import ProfileItem from "../Items/Profile/ProfileItem";

const useStyles = makeStyles((theme) => ({
    styles,
    root: {
        margin: "0 auto",
        marginTop: theme.spacing(12),
    },
}));

function ProfilesList(props) {
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const classes = useStyles();
    const {user, userData} = props;

    const getHeaderByType = (role) => {
        switch (role.toLowerCase()) {
            case "student":
                return (
                    <CardHeader color="success">
                        <h4 className={classes.cardTitleWhite}>,   </h4>
                        <p className={classes.cardCategoryWhite}>
                                   ""
                        </p>
                    </CardHeader>
                );
            case "customer":
                return (
                    <CardHeader color="success">
                        <h4 className={classes.cardTitleWhite}>,    </h4>
                        <p className={classes.cardCategoryWhite}>
                                   ""
                        </p>
                    </CardHeader>
                );
            case "mentor":
                return (
                    <CardHeader color="success">
                        <h4 className={classes.cardTitleWhite}>,    </h4>
                        <p className={classes.cardCategoryWhite}>
                                   ""
                        </p>
                    </CardHeader>
                );
        }
    }

    const getFindTypeUser = (role) => {
        switch (role.toLowerCase()) {
            case "student":
                return "Mentor";
            case "customer":
                return "Mentor";
            case "mentor":
                return "Student";
        }
    }

    const useItems = () => {
        const [items, setItems] = useState([])
        useEffect(() => {
            const unsubscribe = firestore
                .collection("users")
                .where("role", "==", getFindTypeUser(userData.role))
                // .orderBy("createdAt");
                .get()
                .then(snapshot => {
                    const listItems = snapshot.docs
                        .filter((doc) => doc.id != user.uid)
                        .map(doc => ({
                            id: doc.id,
                            ...doc.data(),
                        }))
                    setItems(listItems)
                    setLoading(false);
                }, (error) => {
                    setLoading(false);
                    setError(error);
                })
            return () => unsubscribe
        }, [])
        return items
    }
    const profiles = useItems();

    if (error) {
        return (
            <EmptyState
                image={<ErrorIllustration/>}
                title="   ."
                description="-       ."
                button={
                    <Fab
                        variant="extended"
                        color="primary"
                        onClick={() => window.location.reload()}
                    >
                        <Box clone mr={1}>
                            <RefreshIcon/>
                        </Box>
                        
                    </Fab>
                }
            />
        );
    }

    if (loading) {
        return <Loader/>;
    }

    if (profiles.length >= 1) {
        return (
            <Grid item container xs={12} sm={12} md={10} lg={8} className={classes.root}>
                <Card>
                    {getHeaderByType(userData.role)}
                    <List>
                        {profiles.map((profile, i) => (
                            <ListItem
                                divider={i < profiles.length - 1}
                                key={profile.id}>
                                <ProfileItem profile={profile}/>
                            </ListItem>
                        ))}
                    </List>
                </Card>
            </Grid>
        );
    }

    return (
        <EmptyState
            image={<NoDataIllustration/>}
            title="No orders"
            description=", ,    ("
        />
    );
}

export default ProfilesList;

